<template>
  <div>
    <div class="left">
      <div class="title">Themis</div>
      <ul class="menu">
        <li class="gongneng"><span class="span">功能</span></li>
        <li class="else" @click="changeLinkStyle"><router-link to="/dp_index/introduction" class="span" >Themis简介</router-link></li>
        <li class="else" @click="changeLinkStyle"><router-link to="/dp_index/new_tr" class="span" >新建事务</router-link></li>
        <li class="else" @click="changeLinkStyle"><router-link to="/dp_index/history_tr" class="span" >历史事务</router-link></li>
        <li class="else" @click="changeLinkStyle"><router-link to="/dp_index/user_data" class="span" >个人资料</router-link></li>
      </ul>
    </div>
    <div class="top">
      <span class="username">{{ userName }}</span>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'DP-Index',
  data() {
    return {
      userId: sessionStorage.getItem('userId'),
      userName: "XXXXXX",
      email: 'XXXXXXXXX@XX.XXX',
      registerTime: 'XXXX-XX-XX XX:XX:XX'
    }
  },
  methods: {
    changeLinkStyle(e) {
      const active = document.querySelector('.active')
      if(active !== null) {
        active.classList.remove('active')
      }
      e.target.parentNode.classList.add('active')
    }
  },

  created() {
    axios({
        method: 'get',
        url: '/api/dp/info/' + this.userId,
        headers: {
          token: sessionStorage.getItem('token')
        }
      }).then(res => {
        console.log(res.data)
        if(res.data.code == 0) {
          this.$message.error('用户异常，请重新登录')
          this.$router.push('/')
        }
        this.userId = res.data.data.id
        this.userName = res.data.data.username
        this.email = res.data.data.email
        this.registerTime = res.data.data.registerTime.replace("T", " ")
      }).catch(() => {
        this.$message.error('用户异常，请重新登录')
        this.$router.push('/')
      })
  }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: url("../../images/rightBoxBGI.png") no-repeat;
  background-size: cover;
} 


.left {
  position: fixed;
  height: 100%;
  width: 250px;
  background-color: rgb(33, 33, 33);
}

.top {
  position: absolute;
  left: 250px;
  height: 100px;
  width: 1670px;
  background-color: rgb(219, 215, 215);
}

.right {
  position: absolute;
  left: 250px;
  top: 100px;
}

.left .title {
  position: absolute;
  left: 30px;
  top: 30px;
  font-size: 50px;
  color: white;
}

.left ul {
  position: absolute;
  top: 220px;
  list-style-type: none;
}

.left ul .gongneng {
  background-color: rgb(30, 30, 30);
}

.left ul li .span {
  display: inline-block;
  text-decoration: none;
  padding-top: 30px;
  width: 250px;
  height: 100px;
  font-size: 30px;
  text-align: center;
  color: rgb(80, 80, 80);
}

.left ul .else :hover {
  background-color: rgb(19, 156, 149);
}

.active {
  background-color: rgb(19, 156, 149);
}

.top .username {
  position: absolute;
  left: 1350px;
  top: 35px;
  width: 250px;
  height: 30px;
  text-align: center;
  font-size: 20px;
  overflow: hidden;
}

</style>