<template>
  <div class="container">
    <h3>调查员注册</h3>
    <span class="skip">已有账号？<router-link class="a" to="/">立即登录</router-link></span>
      <div data-prop="username" class="form-item left">
        <input name="username" type="text" placeholder="设置用户名称" autocomplete="off" @change="verifyName">
        <span class="msg"></span>
      </div>
      <div data-prop="email" class="form-item left">
        <input name="email" type="text" placeholder="输入用户邮箱" @change="verifyEmail">
        <span class="msg"></span>
      </div>
      <div data-prop="password" class="form-item left">
        <input name="password" type="password" placeholder="设置6至20位字母、数字和符号组合" @change="verifyPwd">
        <span class="msg"></span>
      </div>
      <div data-prop="confirm" class="form-item left">
        <input name="confirm" type="password" placeholder="请再次输入上面密码" @change="verifyConfirm">
        <span class="msg"></span>
      </div>
      <div data-prop="value1" class="form-item right">
        <input name="value1" type="password" placeholder="属性1" @change="verifyValue1">
        <span class="msg"></span>
      </div>
      <div data-prop="value2" class="form-item right">
        <input name="value2" type="password" placeholder="属性2" @change="verifyValue2">
        <span class="msg"></span>
      </div>
      <div data-prop="value3" class="form-item right">
        <input name="value3" type="password" placeholder="属性3" @change="verifyValue3">
        <span class="msg"></span>
      </div>
      <div class="form-item button">
        <button class="submit" @click="userRegister">下一步</button>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name:'IN-RegisterView',
  data() {
    return {
      
    }
  },
  methods: {
    verifyName() {
      const username = document.querySelector('[name=username]')
      const span = username.nextElementSibling
      const reg = /^[a-zA-Z0-9-_]{6,15}$/
      if (!reg.test(username.value) && username.value != "") {
          span.innerText = '输入不合法,6~15位字母数字下划线组成'
          return false
      }
      span.innerText = ''
      return true
    },
    verifyEmail() {
      const email = document.querySelector('[name=email]')
      const span = email.nextElementSibling
      const reg = /^\w{3,}(\.\w+)*@[A-z0-9]+(\.[A-z]{2,5}){1,2}$/
      if (!reg.test(email.value) && email.value != "") {
          span.innerText = '输入邮箱不合法'
          return false
      }
      span.innerText = ''
      return true
    },
    verifyPwd() {
      const password = document.querySelector('[name=password]')
      const span = password.nextElementSibling
      const reg = /^[a-zA-Z0-9-_]{6,16}$/
      if (!reg.test(password.value) && password.value != "") {
          span.innerText = '输入不合法,6~16位数字字母符号组成'
          return false
      }
      span.innerText = ''
      return true
    },
    verifyConfirm() {
      const confirm = document.querySelector('[name=confirm]')
      const span = confirm.nextElementSibling
      if (confirm.value !== document.querySelector('[name=password]').value && confirm.value != "") {
          span.innerText = '两次密码输入不一致'
          return false
      }
      span.innerText = ''
      return true
    },
    verifyValue1() {
      const value1 = document.querySelector('[name=value1]')
      const span = value1.nextElementSibling
      if (value1.value === "") {
        span.innerText = '属性不能为空'
        return false
      }
      span.innerText = ''
      return true
    },
    verifyValue2() {
      const value2 = document.querySelector('[name=value2]')
      const span = value2.nextElementSibling
      if (value2.value === "") {
        span.innerText = '属性不能为空'
        return false
      }
      span.innerText = ''
      return true
    },
    verifyValue3() {
      const value3 = document.querySelector('[name=value3]')
      const span = value3.nextElementSibling
      if (value3.value === "") {
        span.innerText = '属性不能为空'
        return false
      }
      span.innerText = ''
      return true
    },
    userRegister() {
      if (this.verifyName() && this.verifyEmail() && this.verifyPwd() && this.verifyConfirm()&& this.verifyValue1()&& this.verifyValue2()&& this.verifyValue3()) {
        const data = {
          username: document.querySelector('[name=username]').value,
          email: document.querySelector('[name=email]').value,
          password: document.querySelector('[name=password]').value,
          attr1: document.querySelector('[name=value1]').value,
          attr2: document.querySelector('[name=value2]').value,
          attr3: document.querySelector('[name=value3]').value
        }
        axios({
          method: 'post',
          url: '/api/in/register',
          data: data
        }).then(result => {
          const code = result.data.code
          if(code == 0) {
            this.$message.error('注册失败')
          } 
          else if(code == 1) {
            sessionStorage.setItem('email', result.data.data.email)
            sessionStorage.setItem('username', result.data.data.username)
            sessionStorage.setItem('password', result.data.data.password)
            sessionStorage.setItem('cre1', result.data.data.cre)
            sessionStorage.setItem('prk', result.data.data.prk)
            axios({
              method: 'post',
              url: '/api2/in/register',
              data: data
            }).then(result => {
              const code = result.data.code
              if(code == 0) {
                this.$message.error('注册失败')
              }
              else if(code == 1) {
                sessionStorage.setItem('cre2', result.data.data.cre)
                
                this.$router.push('/in_transfer')
                this.$message({
                  showClose: true,
                  message: '注册成功',
                  type: 'success'
                });
              }
            })
          }
        }).catch(() => {
          this.$message.error('注册失败')
        })
      }
    }
  }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url(../../images/sign-background-image.jpg);
  background-repeat: no-repeat;
}

.container {
  position: absolute;
  left: 460px;
  top: 100px;
  height: 600px;
  width: 1000px;
  border: 4px solid #ebe5ff;
  background: rgba(0, 0, 0, 0.6);
}

.container form {
  padding-top: 40px;
}

.container .left {
  position: relative;
  left: 150px;
  padding-top: 40px;
  width: 300px;
  height: 100px;
}

.container .right {
  position: relative;
  left: 550px;
  bottom: 350px;
  padding-top: 40px;
  width: 300px;
  height: 100px;
}


.container h3 {
  position: relative;
  right: -15px;
  top: 20px;
  color: white;
  font-size: 40px;
}

.container .skip {
  position: relative;
  left: 800px;
  bottom: 20px;
  color: white;
}

.container .skip .a {
  text-decoration: none;
  color: antiquewhite;
  text-align: center;
}

.container .form-item input {
  width: 300px;
  height: 50px;
  border: 1px solid #e4e4e4;
  border-radius: 25px;
  padding-left: 40px;
}

.container .form-item .msg {
  color: #ff4d4f;
  position: absolute;
  left: 2px;
  top: 52px;
}

.container .button {
  position: absolute;
  left: 300px;
  bottom: 50px;
}

.container .form-item button {
  width: 300px;
  height: 50px;
  background: #27BA9B;
  border-radius: 25px;
  display: block;
  font-size: 16px;
  color: #fff;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
}
</style>