<template>
  <div>
    <div class="themis">Themis</div>
    <div class="box">
    <h1>用户登录</h1>
    <div>
      <input class="username" type="text" placeholder="请输入用户名">
      <br>
      <input class="password" type="password" placeholder="请输入密码">
      <button @click="login" class="submit">登录</button>
    </div>
    <div>
      <router-link to="/dp_register" class="sign">数据提供者注册</router-link>
      <router-link to="/in_register" class="in_register">调查员注册</router-link>
      <router-link to="/forgetPassword" class="forgetpassword">忘记密码</router-link>
    </div>
    </div>
  </div>  
</template>

<script>
import axios from 'axios'
export default {
  name:'LoginView',
  data() {
    return {

    }
  },
  methods: {
    login(){
      const username = document.querySelector('.username').value
      const password = document.querySelector('.password').value
      axios({
        method: 'post',
        url:'/api/login',
        data: {
          username,
          password
        }
      }).then(result => {
        const code = result.data.code
        if(code == 0) {
          this.$message.error(result.data.msg)
        }
        else if(code == 1) {
          sessionStorage.setItem('token', result.data.data.token)
          sessionStorage.setItem('userId', result.data.data.id)
          sessionStorage.setItem('userName', result.data.data.username)
          this.$router.push('/dp_index')
          this.$message({
            showClose: true,
            message: '数据提供者登陆成功',
            type: 'success'
          });
        }
        else if(code == 2) {
          sessionStorage.setItem('token', result.data.data.token)
          sessionStorage.setItem('userId', result.data.data.id)
          this.$router.push('/in_index')
          this.$message({
            showClose: true,
            message: '调查员登陆成功',
            type: 'success'
          });
        }
      }).catch((error) => {
        console.log(error)
        this.$message.error('登陆失败')
      })
    }
  }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: url('../images/sign-background-image.jpg') no-repeat;
  background-size: cover;
}

.themis {
  position: absolute;
  left: 800px;
  padding-top: 40px;
  font-size: 100px;
}

.box {
  position: absolute;
  left: 630px;
  top: 200px;
  text-align: center;
  background: rgba(0, 0, 0, 0.6);

  width: 650px;
  height: 500px;

  padding: 30px 30px;
}

.box h1 {
  color: white;
  font-size: 50px;
  padding-top: 20px;
  padding-bottom: 40px;
}

.box input {
  padding-left: 30px;
  margin-bottom: 30px;

  background: rgba(0, 0, 0, 0);
  width: 400px;
  height: 50px;

  border-radius: 25px;
  color: white;
}

.box .index {
  color: white;
  text-decoration: none;
  position: relative;
  left: 290px;
}

.box .submit {
  width: 400px;
  height: 60px;
  background-color: white;
  font-size: 40px;

  border-radius: 30px;
  margin-bottom: 50px;
}

.box .sign {
  float: left;
  color: white;
  text-decoration: none;
  padding-left: 40px;
}

.box .in_register {
  float: left;
  color: white;
  text-decoration: none;
  padding-left: 100px;
}

.box .forgetpassword {
  float: right;
  color: white;
  text-decoration: none;
  padding-right: 60px;
}
</style>