<template>
  <div class="box">
    <h1>分布式匿名凭证生成</h1>
    <div>
      <div>
        <span class="cre-inf displayNone">您的凭证(请妥善保存)</span>
        <textarea class="cre displayNone" name="cre" placeholder="123456789"></textarea>
        <span class="prk-inf displayNone">您的密钥(请妥善保存)</span>
        <textarea class="prk displayNone" name="cre" placeholder="123456789"></textarea>
      </div>
      
      <button class="submit1" @click="generateDAC()">生成</button>
    </div>
    <div>
      <button class="transfer displayNone" @click="toLoginIndex()">我已保存，返回登录页面</button>
    </div>
</div>
</template>

<script>
import axios from 'axios'
import Web3 from 'web3'
export default {
  name:'IN-Transfer',
  data() {
    return {
      
    }
  },
  methods: {
    generateDAC() {
      axios({
        url: '/api/in/credit',
        method: 'post',
        data: {
          cre1: sessionStorage.getItem('cre1'),
          cre2: sessionStorage.getItem('cre2'),
          username: sessionStorage.getItem('username'),
          password: sessionStorage.getItem('password'),
          email: sessionStorage.getItem('email'),
        }
      }).then(result => {
        document.querySelector('.submit1').classList.add('displayNone')
        document.querySelector('.cre-inf').classList.remove('displayNone')
        document.querySelector('.prk-inf').classList.remove('displayNone')
        document.querySelector('.transfer').classList.remove('displayNone')

        const prk = document.querySelector('.prk')
        const cre = document.querySelector('.cre')
        prk.classList.remove('displayNone')
        cre.classList.remove('displayNone')
        cre.value = result.data.data.cre
        prk.value = result.data.data.cre
        
        this.uploadBlock({
          type: 'type',
          para1: 'para1',
          para2: 'para2',
          ts: '2024-05-10',
          sign: 'sign'
        })

      }).catch(() => {
        this.$message.error('生成凭证失败')
      })
        
    }, 
    toLoginIndex() {
      this.$router.push('/')
    },
    
    uploadBlock(data) {
      const web3 = new Web3(Web3.givenProvider || "ws://localhost:7545")
      web3.eth.sendTransaction({
        from: '0xA6a48778Ff91807DAA75DaE07F5e13b8B9000996',//如果from为空，默认取this.web3.eth.defaultAccount
        to: '0x0043A755399603686567334bD4F1F507F8647E03',
        value: 1000000000000000, //发送的金额，这里是0.01 ether
        gas: 121000, //一个固定值，可选
        data: this.stringtoHex(JSON.stringify(data))
      }).then(function (receipt) {
        //待区块确认后会回调，通过receipt判断交易是否成功
        console.log("交易状态：", receipt.status)
      })
    },

    stringtoHex(str) {
      var val = "0x";
      for (var i = 0; i < str.length; i++) {
          if (val == "")
              val = str.charCodeAt(i).toString(16);
          else
              val += str.charCodeAt(i).toString(16);
      }
      console.log(val)
      return val
    }
  }
}
</script>

