  background-color: rgb(170, 247, 242);
  width: 420px;
  height: 130px;
  border-radius: 10px;
}

.box .prk-inf {
  position: absolute;
  left: 250px;
  top: 295px;
  color: white;
}

.box .prk {
  position: absolute;
