<template>
  <div class="box">
    <h1>分布式匿名凭证生成</h1>
    <div>
      <div>
        <textarea class="password"  cols="60px" rows="10px" placeholder="                     凭证请妥善保管" name="cre"></textarea>
      </div>
      <br>
      <button class="submit" @click="generateDAC()">生成</button>
    </div>
    <div>
      <router-link class="forgetpassword" to="/">我已保存</router-link>
    </div>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name:'DP-Transfer',
  data() {
    return {
      
    }
  },
  methods: {
    generateDAC() {
      axios({
        url: '/api/dp/credit',
        method: 'post',
        data: {
          cre1: sessionStorage.getItem('cre1'),
          cre2: sessionStorage.getItem('cre2'),
          username: sessionStorage.getItem('username'),
          password: sessionStorage.getItem('password'),
          email: sessionStorage.getItem('email'),
        }
      }).then(result => {
        console.log(result.data.data)
        document.querySelector('.password').value = result.data.data.cre
      }).catch(() => {
        this.$message.error('生成凭证失败')
      })
    }
  }
}
</script>

